@use "../util/functions.scss" as *;
@use "../util/breakpoints.scss" as *;

header {
  width: 100%;
  margin: rem(24) auto;
  max-width: rem(327);
  display: flex;
  justify-content: space-between;

  @include breakpoints(medium) {
    margin-top: 0;
    max-width: rem(540);
  }
  @include breakpointsDown(small) {
    max-width: rem(290);
  }
}
